import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { useHistory, Redirect } from "react-router-dom";
import {
  CButton,
  CRow,
  CCol,
  CCard,
  CCardBody,
  CCardHeader,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
} from "@coreui/react";
import moment from "moment";
import {
  RESET_DATA_EXAM,
  RESET_DATA_SOAL,
  RESET_USER,
} from "../../../redux/actions";
import endpoints from "../../../utils/endpoints";
import request from "../../../utils/request";
import Header from "../Header";

function ExamConfirmation() {
  const dataExam = useSelector((state) => state.dataExam);
  const { profile } = useSelector((state) => state.user);
  const [isLoading, setIsLoading] = useState(false);
  const history = useHistory();
  const [alertMessage, setAlertMessage] = useState("");
  const [modal, setModal] = useState(false);
  const finishTest = async () => {
    setIsLoading(true);
    try {
      const config = {
        ...endpoints.startExamUser,
        data: {
          id: dataExam.currentUser.id,
          waktu_selesai: moment().format("YYYY-MM-DD HH:mm"),
          status_ujian: "selesai",
        },
      };
      const res = await request(null, config);
      setAlertMessage(
        "ðŸŽ‰ðŸ‘Ujian telah berhasil diselesaikan,ðŸ‘ðŸŽ‰\n terima kasih telah mengikuti ujian!ðŸ™"
      );
      setModal(true);
      setTimeout(() => {
        setIsLoading((state) => !state);
        setModal((state) => !state);
        history.push("/login");
      }, 5000);
    } catch (error) {
      setAlertMessage(error.error.response.data.messages);
      setModal(true);
      setTimeout(() => {
        setIsLoading((state) => !state);
        setModal((state) => !state);
        history.push("/login");
      }, 5000);
    }
  };

  const isLoggedIn = () => (profile.nama ? true : false);
  const toggle = () => {
    setModal((state) => !state);
  };

  if (!isLoggedIn()) {
    return <Redirect to="/login" />;
  }

  return (
    <>
      <Header>
        <CRow className="d-flex justify-content-center">
          <CCol md={6}>
            <CCard className="card-rounded shadow border-white d-flex flex-wrap mt-2">
              <CCardHeader className="d-flex direction-row justify-content-between align-items-center card-rounded border-0">
                <h3>Konfirmasi Tes</h3>
              </CCardHeader>
              <CCardBody className="pt-0">
                <div className="mb-4">
                  Terima kasih telah berpartisipasi dalam tes ini. Silahkan
                  tekan tombol{" "}
                  <span className="text-white px-1 bg-danger">SELESAI TES</span>{" "}
                  untuk mengakhiri tes.
                  {/* atau tekan{" "} */}
                  {/* <span className="text-white px-1 bg-success">KEMBALI</span>{" "}
                untuk kembali ke halaman test. */}
                </div>
                <div className="d-flex direction-row justify-content-center">
                  <CButton
                    block
                    color="danger"
                    className="mx-1"
                    size="lg"
                    shape="pill"
                    disabled={isLoading}
                    onClick={() => finishTest()}
                  >
                    SELESAI TES
                  </CButton>
                  {/* <Link to={"/exam"}>
                  <CButton
                    block
                    color="success"
                    className="mx-1"
                    size="lg"
                    shape="pill"
                  >
                    KEMBALI
                  </CButton>
                </Link> */}
                </div>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </Header>
      <CModal show={modal} onClose={toggle} closeOnBackdrop={false}>
        <CModalHeader closeButton>Informasi</CModalHeader>
        <CModalBody>{alertMessage}</CModalBody>
        <CModalFooter>
          <CButton color="primary" onClick={toggle}>
            OK
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  );
}

export default ExamConfirmation;
